<template>
    <div class="">
          <div class="container">
              <section class="_header" v-show="!isLoading">
                  <div class="block-h">
                      <div class="logo pointer" @click="go('home')">zebest</div>
                      <div class="buttons" v-if="isConnected">
                          <input type="hidden" id="toCopy" :value="`${ghost.payment_link}`">
                          <div class="item mr-2" @click="loadDatas()"><i class="feather icon-repeat"></i></div>
                          <div class="item mr-2" @click="displayModal()"><i class="feather icon-send"></i></div>
                          <div class="item mr-2" @click.stop.prevent="copyLink()"><i class="feather icon-copy"></i></div>
                          <div class="item mr-2" @click="logout()"><i class="feather icon-log-out"></i></div>
                      </div>
                  </div>
              </section>
          </div>

        <div class="mypage">
          <div v-show="isLoading" class="mt-60 loading">
              <izy-hollow-loading loading :colour="'#46D465'" />
          </div>

          <div class="container" v-if="isConnected && !isLoading">
              <div class="balance">
                  <div class="message">Bienvenue <span>{{ ghost.first_name }}</span>, <br/>votre solde est de <span>{{ ghost.balance }} &euro;</span></div>
              </div>

              <div class="card mt-20">
                  <div :class="['card-header', showTransactions ? 'active' : '']" @click="displayTransactions()">
                      <i class="feather icon-activity"></i>
                      <span>Mes transactions</span>
                  </div>
                  <div class="card-body" v-if="showTransactions">
                      <div class="profile-box">
                          <div
                              class="profile-item"
                              v-for="t in transactions"
                              :key="t.id"
                          >
                              <div class="dat mt-20 text-center">{{ t.created_at | date }}</div>
                              <div
                                class="content"
                                v-for="item in t.items"
                                :key="item.id"
                              >
                                  <div :class="['icon-car', item.cash_flow == 'IN' ? 'in' : 'out']">
                                      <i :class="['feather', item.cash_flow == 'IN' ? 'icon-trending-up' : 'icon-trending-down']"></i>
                                  </div>
                                  <div class="label">
                                      <span class="wallet">{{ item.cash_flow == 'IN' ? 'Donation' : 'Retrait' }}</span>
                                      <span class="date">{{ displayFromNow(item.created_at) }}</span>
                                  </div>
                                  <div :class="['icon-cir', item.cash_flow == 'IN' ? 'in' : 'out']">
                                    <span>{{ item.amount }} &euro;</span>
                                    <i class="feather icon-chevron-right"></i>
                                  </div>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>

              <div class="card">
                  <div :class="['card-header', showRetrait ? 'active' : '']" @click="displayRetrait()">
                      <i class="feather icon-dollar-sign"></i>
                      <span>Faire un retrait</span>
                  </div>
                  <div class="card-body" v-if="showRetrait">

                      <div class="block">
                        <h2 class="mt-10">faire un retrait</h2>

                        <div class="btns-block mt-20">
                            <input
                                class="b-r br-10"
                                :placeholder="200"
                                name="amount"
                                v-model="rhost.amount"
                                type="number"
                            >
                            <button class="rond">
                                <i class="feather icon-repeat"></i>
                            </button>
                            <button class="btn btn-outline b-l br-10">
                                {{ xaf_amount }} FCFA
                            </button>
                        </div>

                        <div class="p mt-20">1.00 &euro; = {{ taux_xaf }} FCFA <br/> *Le taux de change varie en fonction du mode d'envoi et de paiement.</div>
                        <div class="recaps mt-20">
                            <div class="recap-line">
                                <div class="label">Frais de transfert</div>
                                <div class="value">+ {{ transfert_amount }} EUR</div>
                            </div>
                            <div class="recap-line">
                                <div class="label">Total du transfert</div>
                                <div class="value">{{ total_euro_amount }} EUR</div>
                            </div>

                            <div class="recap-line">
                                <div class="label">Montant  reevoir</div>
                                <div class="value">{{ net_a_recevoir }} FCFA</div>
                            </div>
                            <div class="divider"></div>
                            <div class="recap-line">
                                <div class="label">Disponibilit</div>
                                <div class="value">En quelques minutes</div>
                            </div>
                        </div>
                        <div class="mt-20 text-center">
                            <button class="btn btn-block btn-primary br-100" @click="retrait()" :disabled="rhost.amount==0">
                                Valider
                            </button>
                        </div>
                      </div>

                  </div>
              </div>

              <div class="card">
                  <div :class="['card-header', showDon ? 'active' : '']" @click="displayDon()">
                      <i class="feather icon-award"></i>
                      <span>Faire un don</span>
                  </div>
                  <div class="card-body" v-if="showDon">
                    <div class="block">
                      <h2>faire un don</h2>

                      <div class="list-ronds mt-20">
                          <div :class="['rond-item', dhost.amount == 1 ? 'active' : '']" @click="selectMontant(1)">1 &euro;</div>
                          <div :class="['rond-item', dhost.amount == 5 ? 'active' : '']" @click="selectMontant(5)">5 &euro;</div>
                          <div :class="['rond-item', dhost.amount == 10 ? 'active' : '']" @click="selectMontant(10)">10 &euro;</div>
                          <div :class="['rond-item', dhost.amount == 50 ? 'active' : '']" @click="selectMontant(50)">50 &euro;</div>
                          <div :class="['rond-item', dhost.amount == 100 ? 'active' : '']" @click="selectMontant(100)">100 &euro;</div>
                      </div>

                      <div class="diviseur">
                          <div class="divider"></div>
                          <div class="rond">Ou</div>
                          <div class="divider"></div>
                      </div>

                      <form class="_form mt-20" @submit.prevent>
                        <div class="form-group">
                           <select
                               name="receiver"
                               v-model="dhost.receiver"
                               class="form-control form-control-lg input"
                           >
                           <option value="">Slectionner un crateur de contenu</option>
                           <option
                               v-for="c in creators"
                               :key="c.id"
                               :value="c.id"
                           >{{ c.first_name }} {{ c.last_name }}</option>
                           </select>
                        </div>

                        <div class="form-group mt-20">
                           <label for="sender_country">Votre pays de rsidence</label>
                           <select
                               name="sender_country"
                               v-model="dhost.sender_country"
                               class="form-control form-control-lg input"
                           >
                           <option value="">Slectionner votre pays</option>
                           <option
                               v-for="(c, index) in countries"
                               :key="index+1"
                               :value="c.code"
                           >{{ c.name }}</option>
                           </select>
                        </div>

                        <div class="form-group mt-20">
                           <label for="amount">Montant de la donation (en )</label>
                           <input type="number"
                               name="amount"
                               placeholder="5 "
                               class="form-control form-control-lg input"
                               v-model="dhost.amount"
                           >
                        </div>

                         <div class="mt-10 mb-20 text-center">
                             <button class="btn btn-primary br-100" @click="faireundon()" :disabled="dhost.amount==''">
                                 Ovations de {{ dhost.amount != '' ? dhost.amount : 0 }} &euro;
                             </button>
                         </div>
                       </form>
                    </div>
                  </div>
              </div>

              <div class="card">
                  <div :class="['card-header', showMyAccount ? 'active' : '']" @click="displayMyAccount()">
                      <i class="feather icon-user"></i>
                      <span>Mon compte</span>
                  </div>
                  <div class="card-body" v-if="showMyAccount">
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-6">
                              <form class="_form mt-20 dark" @submit.prevent>
                                  <div class="content-profile-photo photo">
                                      <input type="file" name='image' id="imgupload" accept="image/*" style="display:none" @change="handleFile">
                                      <div class="photo pointer">
                                          <img class="image" id="image" :src="ghost.image"/>
                                      </div>
                                      <button class="btn btn-primary mt-20 br-100" id="OpenImgUpload" @click="openUploadFile()">Nouvelle photo</button>
                                  </div>
                                <div class="row">
                                    <div class="col-sm-12">
                                        <div class="form-group mt-20">
                                           <label for="name">Nom</label>
                                           <input type="text"
                                               name="last_name"
                                               placeholder="Nom"
                                               class="form-control form-control-lg input"
                                               v-model="ghost.last_name"
                                               v-validate="'required'"
                                           >
                                             <span class="has-error">{{ errors.first('last_name') }}</span>
                                        </div>
                                    </div>
                                    <div class="col-sm-12">
                                        <div class="form-group mt-20">
                                           <label for="name">Prnom</label>
                                           <input type="text"
                                               name="first_name"
                                               placeholder="Prnom"
                                               class="form-control form-control-lg input"
                                               v-model="ghost.first_name"
                                               v-validate="'required'"
                                           >
                                             <span class="has-error">{{ errors.first('first_name') }}</span>
                                        </div>
                                    </div>
                                </div>

                                <div class="form-group mt-20">
                                    <label for="url">Ton lien personnalis</label>
                                    <div class="content">
                                        <div class="teal">zebest.com/</div>
                                        <input type="text"
                                            name="username"
                                            placeholder="tonnomdecreateur"
                                            class="form-control form-control-lg dark"
                                            v-model="ghost.username"
                                            v-validate="'required'"
                                        >
                                        <div class="check"><i class="feather icon-check primary"></i></div>
                                    </div>
                                    <span class="has-error">{{ errors.first('username') }}</span>
                                </div>

                                 <div class="form-group mt-20">
                                    <label for="about">A propos</label>
                                        <textarea
                                            name="bio"
                                            placeholder="Bonjour, j'ai cr cette page pour ceux qui veulent me soutenir."
                                            class="form-control textarea form-control-lg"
                                            v-model="ghost.bio" rows="5" cols="33" v-validate="'required'"
                                        ></textarea>
                                      <span class="has-error">{{ errors.first('bio') }}</span>
                                 </div>
                                 <div class="mt-20 mb-20 text-center">
                                     <button class="btn btn-primary br-100" @click="saveProfile()">
                                         Enregistrer
                                     </button>
                                 </div>
                               </form>
                            </div>

                            <div class="col-sm-6">
                              <h3 class="mt-20">Modification mot de passe</h3>
                              <form class="_form mt-20" @submit.prevent>
                                 <div class="form-group">
                                    <input type="password"
                                        name="new_password1"
                                        placeholder="Nouveau mot de passe"
                                        class="form-control form-control-lg input"
                                        v-model="phost.new_password1"
                                        v-validate="'min:8'"
                                    >
                                    <span class="has-error">{{ errors.first('new_password1') }}</span>
                                 </div>

                                 <div class="form-group">
                                    <input type="password"
                                        name="new_password2"
                                        placeholder="Confirmation nouveau mot de passe"
                                        class="form-control form-control-lg input"
                                        v-model="phost.new_password2"
                                        v-validate="'min:8'"
                                    >
                                      <span class="has-error">{{ errors.first('new_password2') }}</span>
                                 </div>

                                 <div class="mt-20 mb-20 text-center">
                                     <button class="btn btn-primary br-100" @click="resetPassword()">
                                         Modifier
                                     </button>
                                 </div>
                               </form>
                            </div>
                        </div>
                    </div>
                  </div>
              </div>

              <div class="card"><!-- @click="displaySettings()" -->
                  <div :class="['card-header', showSettings ? 'active' : 'no-cursor']">
                      <i class="feather icon-settings"></i>
                      <span>Paramtres</span>
                  </div>
                  <div class="card-body" v-if="showSettings">
                    <div class="block">
                      <h2>Choix de la devise</h2>

                      <form class="_form mt-20 dark" @submit.prevent>
                         <div class="form-group">
                            <label for="devise">En quelle devise souhaiterais-tu tre pay(e) ?</label>
                            <select
                                name="currency"
                                v-model="host.currency"
                                class="form-control form-control-lg input"
                            >
                            <option
                                v-for="r in devises"
                                :key="r.id"
                                :value="r.id"
                            >{{ r.name }}</option>
                            </select>
                         </div>

                         <div class="form-group mt-20">
                             <label for="phone">Connecter votre numro de portefeuille mobile</label>
                             <input type="text"
                                 name="phone"
                                 placeholder="+237651545258"
                                 class="form-control form-control-lg input"
                                 v-model="host.phone"
                             >
                         </div>

                         <div class="mt-20 text-center">
                             <button class="btn btn-primary br-100" @click="saveWallet()">
                                 Valider
                             </button>
                         </div>
                       </form>
                    </div>
                  </div>
              </div>

              <div class="card">
                  <div :class="['card-header', showRS ? 'active' : '']" @click="displayRS()">
                      <i class="feather icon-globe"></i>
                      <span>Rseaux sociaux</span>
                  </div>
                  <div class="card-body" v-if="showRS">
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-6">
                              <form class="_form" @submit.prevent>
                                    <div class="form-group mt-20">
                                        <label for="name">Nom du rseau social</label>
                                        <input type="text"
                                            name="name"
                                            placeholder="Nom du rseau social"
                                            class="form-control form-control-lg input"
                                            v-model="shost.name"
                                            v-validate="'required'"
                                        >
                                        <span class="has-error">{{ errors.first('name') }}</span>
                                    </div>
                                    <div class="form-group mt-20">
                                        <label for="link">Lien du rseau social</label>
                                        <input type="text"
                                            name="link"
                                            placeholder="Lien du rseau social"
                                            class="form-control form-control-lg input"
                                            v-model="shost.link"
                                            v-validate="'required'"
                                        >
                                        <span class="has-error">{{ errors.first('link') }}</span>
                                    </div>

                                   <div class="mt-20 mb-20 text-center">
                                       <button class="btn btn-primary br-100" @click="saveSocialLink()">
                                           Enregistrer
                                       </button>
                                   </div>
                              </form>
                            </div>
                            <div class="col-sm-6">
                                <div class="social-box mt-20">
                                   <div class="tle bold">Liens de vos rseaux sociaux</div>
                                   <div
                                       class="social-item pointer"
                                       v-for="s in social_links"
                                       :key="s.id"
                                   >
                                     <a :href="s.link" target="_blank">{{ s.link }}</a>
                                   </div>
                                </div>
                            </div>
                        </div>
                    </div>
                  </div>
              </div>
          </div>
          <div class="home" v-if="!isConnected && !isLoading">
            <div class="block">
              <div class="text-center">
                  <h2>faire un don </h2>
                  <h2><span>{{ title_name }}</span></h2>
              </div>

              <div class="list-ronds mt-20">
                  <div :class="['rond-item', dhost.amount == 1 ? 'active' : '']" @click="selectMontant(1)">1 &euro;</div>
                  <div :class="['rond-item', dhost.amount == 3 ? 'active' : '']" @click="selectMontant(3)">3 &euro;</div>
                  <div :class="['rond-item', dhost.amount == 5 ? 'active' : '']" @click="selectMontant(5)">5 &euro;</div>
                  <div :class="['rond-item', dhost.amount == 10 ? 'active' : '']" @click="selectMontant(10)">10 &euro;</div>
                  <div :class="['rond-item', dhost.amount == 50 ? 'active' : '']" @click="selectMontant(50)">50 &euro;</div>
                  <div :class="['rond-item', dhost.amount == 100 ? 'active' : '']" @click="selectMontant(100)">100 &euro;</div>
              </div>

              <div class="diviseur">
                  <div class="divider"></div>
                  <div class="rond">Ou</div>
                  <div class="divider"></div>
              </div>

              <form class="_form mt-20" @submit.prevent>
                <div class="form-group mt-20">
                  <label for="amount">Montant de la donation (en &euro;)</label>
                   <input type="number"
                       name="amount"
                       placeholder="5 "
                       class="form-control form-control-lg input"
                       v-model="dhost.amount"
                   >
                </div>

                <div class="form-group mt-20">
                   <input type="text"
                       name="sender_first_name"
                       placeholder="Votre prnom"
                       class="form-control form-control-lg input"
                       v-model="dhost.sender_first_name"
                   >
                </div>

                <div class="form-group mt-20">
                   <input type="text"
                       name="sender_last_name"
                       placeholder="Votre nom"
                       class="form-control form-control-lg input"
                       v-model="dhost.sender_last_name"
                   >
                </div>

                <div class="form-group">
                   <label for="sender_country">Votre pays de rsidence</label>
                   <select
                       name="sender_country"
                       v-model="dhost.sender_country"
                       class="form-control form-control-lg input"
                   >
                   <option value="">Slectionner votre pays</option>
                   <option
                       v-for="(c, index) in countries"
                       :key="index+1"
                       :value="c.code"
                   >{{ c.name }}</option>
                   </select>
                </div>

                 <div class="mt-10 mt-20 mb-20 text-center">
                     <button class="btn btn-primary br-100" @click="faireundon()" :disabled="dhost.amount==''">
                         Ovations de {{ dhost.amount != '' ? dhost.amount : 0 }} &euro;
                     </button>
                 </div>
               </form>
            </div>
          </div>
        </div>

        <ConfirmModal v-if="showModal" :user="auth"></ConfirmModal>
        <ShareModal v-if="showshareModal" :user="selected"></ShareModal>
        <BancaireModal v-if="showBancaireModal" :user="creator" :donation="donation"></BancaireModal>
    </div>
</template>

<script>
import AuthService from '@/services/auth'
import ConfirmModal from '../users/modals/confirm'
import ShareModal from '../users/modals/share'
import BancaireModal from './modals/confirm'
import DashboardMixins from './mixins'

export default {
    mixins: [DashboardMixins],

    components: { ConfirmModal, BancaireModal, ShareModal },

    computed: {
        auth () { return JSON.parse(localStorage.getItem(this.$config.get('user'))) },
        is_creator () { return this.auth.is_creator },
        payment_link () { return this.auth.payment_link },
        isConnected () { return !_.isEmpty(this.auth) },
    },

    mounted () {
        if (!this.isConnected) {
            this.getCreator()
            this.getTauxChange()
            this.dhost = {  amount: '', receiver: '', sender_first_name: '', sender_last_name: '', sender_country: '' }
        }

        if (this.isConnected) {
            this.loadDatas()
        }
    },

    methods: {
        selectFile () {
            var fileSelect = document.getElementById("fileSelect")
            var fileElem = document.getElementById("fileElem")

            fileSelect.addEventListener("click", function (e) {
                if (fileElem) {
                    fileElem.click()
                }
                e.preventDefault()
            }, false)
        },

        displayModal () {
            this.$store.commit('SET_SHOW_SHARE_MODAL', true)
            this.selected = this.ghost
        },

        loadDatas () {
            this.host.currency = 'EUR'
            this.getProfile()
            this.getWallet()
            this.getSocialLinks()
            this.initDevises()
            this.resetShost()
            this.resetDhost()
            this.getDonations()
            this.getCreators()
            this.getTauxChange()
            this.getTransactions()
            this.getMoneyRequests()
        },

        copyLink () {
            let toCopy = document.querySelector('#toCopy')
            toCopy.setAttribute('type', 'text')
            toCopy.select()

            try {
              document.execCommand('copy')
              this.$swal.success(this.$translate.text('Lien copi'))
              this.isNotCopied = false
            } catch (err) {
              this.isNotCopied = true
              this.$swal.error(this.$translate.text('Lien non copi'))
            }

            /* unselect the range */
            toCopy.setAttribute('type', 'hidden')
            window.getSelection().removeAllRanges()
        },

        /**
         * User signs in
         * @return {void}
         */
        async resetPassword () {
            if (this.phost.new_password1 != '' && this.phost.new_password2 == '') {
                this.$swal.error('Validation', 'Votre confirmation de mot de passe est vide')
            }

            if (this.phost.new_password1 == '' && this.phost.new_password2 != '') {
                this.$swal.error('Validation', 'Votre mot de passe est vide')
            }

            if (this.phost.new_password1 == '' && this.phost.new_password2 == '') {
                this.$swal.error('Validation', 'Bien vouloir saisir le mot de passe et la confirmation')
            }

            if (this.phost.new_password1 != '' && this.phost.new_password2 != '') {
                this.startLoading()

                const response = await this.$api.post('/auth/password/change/', this.phost)
                    .catch(error => {
                        this.stopLoading()
                        console.log('erreur => ', error.response.data)
                        this.$swal.error('Erreur cration compte', error.response.data)
                    })

                if (response) {
                    this.showErrors =  false
                    this.stopLoading()
                    this.phost = { new_password1: '', new_password2: '' }
                    this.$swal.success('Confirmation', 'Votre mot de passe a t mit  jour avec succs')
                    this.loadDatas()
                }
            }

        },

        selectMontant (montant) { this.dhost.amount = montant },

        initDevises() {
            this.devises = [
              { 'id': 'USD', 'name': 'USD - Dollar Amricain'},
              { 'id': 'EUR', 'name': 'EUR - Union Economique Europenne'},
              { 'id': 'XAF', 'name': 'XAF - Franc CFA'},
            ]
        },

        async getTauxChange () {
            this.startLoading()
            const response = await this.$api.get('/payment-api/current-change/')
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste taux de change', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.$store.commit('donations/SET_RATES', response.data.results)
                }
        },

        async saveWallet () {
            this.startLoading()
            const response = await this.$api.put(`/payment-api/wallets/${this.host.id}/`, this.host)
                .catch(error => {
                    this.stopLoading()
                    this.$swal.error('Erreur modification des paramtres', error.response.data.message)
                })

                if (response) {
                    this.stopLoading()
                    this.getWallet()
                    this.$swal.success('Confirmation', 'Modification des paramtres !')
                }
        },

        async getWallet () {
            this.startLoading()
            const response = await this.$api.get('/payment-api/wallets/')
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste des paramtres', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.host = Object.assign({}, response.data.results[0])
                }
        },

        async getTransactions () {
            this.startLoading()
            const response = await this.$api.get('/payment-api/group-transactions/')
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste des transactions', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.transactions = response.data.results
                }
        },

        /**
         * Events listeners
         *
         * @return {void}
         */
        listenToEvents () {
            window.eventBus.$on('loading:show', () => {
                $('#loadingModal').modal('show')
            })

            window.eventBus.$on('loading:hide', () => {
                $('#loadingModal').modal('hide')
            })
        },

        async getProfile () {
            this.startLoading()

            let url = '/user-api/users/' + this.auth.id + '/'
            const response = await this.$api.get(url)
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur chargement de votre profil', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.showErrors =  false
                    AuthService.setUser(response.data)
                    this.ghost = Object.assign({}, response.data)
                    if(this.ghost.image !== null) {
                        this.displayIcon = false
                        $('#image').attr('src', this.ghost.image)
                    }
                }
        },

        async getCreator () {
            this.startLoading()
            let payload = { 'username': this.$route.params.id }
            const response = await this.$api.post('/payment-api/user-infos/', payload)
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur chargement des donnes du crateur de contenu', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.creator = Object.assign({}, response.data)
                }
        },

        async getCreators () {
            this.startLoading()
            const response = await this.$api.get('/user-api/users/')
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste des crateurs des contenus', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.creators = response.data.results.filter(c => c.is_creator == true && this.auth.id != c.id)
                }
        },

        async getDonations (page = 1) {
            this.startLoading()

            let payload = { 'page': page }

            const response = await this.$api.get('/payment-api/donations/', { params: payload })
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste des donations', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.$store.commit('donations/SET_DONATIONS', response.data.results)
                    this.chartData.labels = response.data.results.map(r => r.created_at)
                    this.chartData.datasets = [{ data: response.data.results.map(r => Number.parseInt(r.amount, 10)) }]
                    this.NextDonations = !_.isEmpty(response.data.next)
                    this.PreviousDonations = !_.isEmpty(response.data.previous)
                }
        },

        async getMoneyRequests (page = 1) {
            this.startLoading()

            let payload = { 'page': page }

            const response = await this.$api.get('/payment-api/money-requests/', { params: payload })
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste des donations', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.$store.commit('retraits/SET_RETRAITS', response.data.results)
                    this.NextRetraits = !_.isEmpty(response.data.next)
                    this.PreviousRetraits = !_.isEmpty(response.data.previous)
                }
        },

        handleFile (e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;

            var reader = new FileReader()

            reader.onloadend = function () {
                $('.image').attr('src', reader.result)
            }
            reader.readAsDataURL(files[0])
            this.ghost.image = files[0]
            this.displayIcon = false
        },

        async saveProfile () {
            if (this.ghost.image == null) {
                this.$swal.error('Validation', 'Votre photo de profil est vide')
            } else {
                this.showErrors =  true
                const isValid = await this.$validator.validate()
                if (!isValid) return false

                this.startLoading()
                this.ghost.payment_link = `${this.$config.get('front_url')}${this.ghost.username}`

                let formData = new FormData()
                formData.append('bio', this.ghost.bio)
                formData.append('payment_link', this.ghost.payment_link)
                formData.append('image', this.ghost.image)
                formData.append('last_name', this.ghost.last_name)
                formData.append('first_name', this.ghost.first_name)

                let url = '/user-api/users/' + this.auth.id + '/'
                const response = await this.$api.patch(url, formData)
                    .catch(error => {
                        this.stopLoading()
                        this.$swal.error('Erreur modification du profil', error.response.data.message)
                    })

                    if (response) {
                        this.stopLoading()
                        this.showErrors =  false
                        AuthService.setUser(response.data)
                        this.$swal.success('Confirmation', 'Compte modifi avec succs !')
                        this.getProfile()
                    }
            }
        },

        async faireundon () {
          let payload = {}
          if (!this.isConnected && _.isEmpty(this.dhost.sender_last_name) && !_.isEmpty(this.dhost.sender_first_name)) {
              this.$swal.error('Validation', 'Bien vouloir saisir votre nom avant de valider')
          }

          if (!this.isConnected && !_.isEmpty(this.dhost.sender_last_name) && _.isEmpty(this.dhost.sender_first_name)) {
              this.$swal.error('Validation', 'Bien vouloir saisir votre prnom avant de valider')
          }

          if (!this.isConnected && _.isEmpty(this.dhost.sender_last_name) && _.isEmpty(this.dhost.sender_first_name)) {
              this.$swal.error('Validation', 'Bien vouloir saisir votre nom et prnom avant de valider')
          }

          if (!this.isConnected && this.dhost.sender_last_name!='' && this.dhost.sender_first_name!='') {
              this.startLoading()
              payload = {
                  receiver: this.creator.id,
                  amount: this.donation_total_euro_amount,
                  sender_last_name: this.dhost.sender_last_name,
                  sender_first_name: this.dhost.sender_first_name,
                  sender_country: this.dhost.sender_country,
              }
              let name = this.dhost.sender_last_name + ' ' + this.dhost.sender_first_name
              localStorage.setItem('nom_user', JSON.stringify(name))

              const response = await this.$api.post('/payment-api/donations/', payload)
                  .catch(error => {
                      this.stopLoading()
                      this.$swal.error('Erreur paiement donation', error.response.data.message)
                  })

                  if (response) {
                      this.stopLoading()
                      localStorage.setItem('donation', response.data.donation_id)
                      this.donation = Object.assign({}, response.data)
                      this.$store.commit('SET_SHOW_BANCAIRE_MODAL', true)
                  }
          }

          if (this.isConnected) {
              this.startLoading()
              payload = Object.assign({}, this.dhost)
              const response = await this.$api.post('/payment-api/donations/', payload)
                  .catch(error => {
                      this.stopLoading()
                      this.$swal.error('Erreur paiement donation', error.response.data.message)
                  })

                  if (response) {
                      this.stopLoading()
                      localStorage.setItem('donation', response.data.donation_id)
                      this.donation = Object.assign({}, response.data)
                      this.$store.commit('SET_SHOW_BANCAIRE_MODAL', true)
                  }
          }


        },

        async saveSocialLink () {
            this.showErrors =  true
            const isValid = await this.$validator.validate()
            if (!isValid) return false

            this.startLoading()

            const payload = {  name: this.shost.name, link: this.shost.link }

            const response = await this.$api.post('/user-api/social-links/', payload)
                .catch(error => {
                    this.stopLoading()
                    this.$swal.error('Erreur ajout rseau social', error.response.data.message)
                })

                if (response) {
                    this.stopLoading()
                    this.showErrors =  false
                    this.getSocialLinks()
                    this.resetShost()
                    this.$swal.success('Confirmation', 'Rseau socail ajout avec succs !')
                }
        },

        async getSocialLinks () {
            this.startLoading()

            const response = await this.$api.get('/user-api/social-links/')
                .catch(error => {
                    this.stopLoading()
                    // this.$swal.error('Erreur liste rseaux sociaux', error.response.data.message)
                    console.log(error.response.data)
                })

                if (response) {
                    this.stopLoading()
                    this.social_links = response.data.results
                }
        },

        resetShost () { this.shost = {  name: '', link: '' } },

        resetDhost () {
              let obj = { amount: '', receiver: '', sender_country: '', sender: this.auth.id }
              this.dhost = Object.assign({}, obj)
        },

        logout () {
            AuthService.logout()
            this.go('home')
            // window.setTimeout(location.reload(), 50000)
        },

        async retrait () {
            let montant = Number.parseInt(this.rhost.amount, 10)
            if (montant != 0) {
              this.startLoading()

              const payload = { 'amount': montant }

              const response = await this.$api.post('/payment-api/money-requests/', payload)
                  .catch(error => {
                      this.stopLoading()
                      this.$swal.error('Erreur de retrait', error.response.data.message)
                  })

                  if (response) {
                      this.stopLoading()
                      this.$swal.success('Confirmation', response.data.message)
                      this.rhost.amount = 0
                      this.getMoneyRequests()
                  }
            } else {
              this.$swal.error('Validation', 'Bien vouloir saisir le montant  retirer !')
            }
        },
    }
}
</script>
